.loginscreen{
    position: relative;
    height: 100%;
    background:url("https://images-wixmp-ed30a86b8c4ca887773594c2.wixmp.com/f/f562aaf4-5dbb-4603-a32b-6ef6c2230136/dh0w8qv-9d8ee6b2-b41a-4681-ab9b-8a227560dc75.jpg/v1/fill/w_1192,h_670,q_70,strp/the_netflix_login_background__canada__2024___by_logofeveryt_dh0w8qv-pre.jpg?token=eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..LOYKSxIDqfPwWHR0SSJ-ugGQ6bECF0yO6Cmc0F26CQs")
    center no-repeat ;
    background-size: cover;
}


.loginscreen_gradient{
    width: 100%;
    z-index: 1;
    height: 100vh;
    background: rgba(0, 0, 0, 0.4);
    background-image: linear-gradient(
        to top,
        rgba(0,0,0,0.8) 0%,
        rgba(0,0,0,0) 60%,
        rgba(0,0,0,0.8) 100%

    );
}

.loginscreen_logo{
    position: fixed;
    left: 0;
    width: 150px;
    object-fit: contain;
    padding-left: 20px;
    top: -20px;
}

.loginscreen_button{
    position: fixed;
    right: 20px;
    top: 20px;
    padding: 10px 20px;
    font-size:1rem ;
    color: #fff;
    background-color: #e50914;
    border: none;
    cursor: pointer;
}

.loginscreen_body > h1{
    font-size: 3.125rem;
    margin-bottom: 20px;
}

.loginscreen_body > h2{
    font-size: 2rem;
    font-weight: 400;
    margin-bottom: 30px;
}
.loginscreen_body > h3{
    font-size: 1.3rem;
    font-weight: 400;
    
}

.loginscreen_body{
    position: absolute;
    top: 30%;
   display: flex;
   flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    left: 0;
    right: 0;
    z-index: 1;
    color: #fff;
    padding: 20px;
}

.loginscreen_input >form >input{
    padding: 16px;
    outline-width:0 ;
    height: 48px;
    width: 30%;
    border: none;
    max-width: 600px;
    font-size: 1rem;
    ;

}
.loginscreen_input{
    margin: 20px;
    width: 100%;
    
}

.loginscreen_getStarted{
   
    padding: 15px 20px;
    font-size: 1rem;
    color: #fff;
    background-color: #e50914;
    border: none;
    font-weight: 600;
    cursor: pointer;
}

