 .nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 5rem;
    z-index: 1;


    /* Animation */
    transition-timing-function: ease-in;
    transition: all 0.5s;
 }

 
 

 
 
 .nav_contents{
    display: flex;
    justify-content: space-between;
  
   
 }

 .nav_black{
   background-color: #111;
}

 

 .nav_logo{
    position: fixed;
   left: 0;
   width: 5rem;
  top: 10px;
   object-fit: contain;
   padding-left: 1.25rem;
   cursor: pointer;
  
   
 }
 .nav_avatar{
    cursor: pointer;
    position: fixed;
    right: 1.25rem;
    width: 1.875rem;
    
 }